.slides{
    width: 75%;
    margin-left: 15%;
    display: flex;
    gap: 20px;
    border-radius:0 15px 15px 0;
    overflow-x: scroll;
}
::-webkit-scrollbar {
    width: 0;  
    background: transparent;  
}

.slide img{
    height: 180px;
    width: 278px;
    object-fit: cover;
} 